import React from 'react';
import { Container, Row, Col } from 'reactstrap';

import img1 from '../../../assets/images/ui/peculiarities1.jpg';
import img2 from '../../../assets/images/ui/peculiarities2.jpg';
import img3 from '../../../assets/images/ui/peculiarities3.jpg';

// Особенности
const Peculiarities = () => {
    return (
        <div id="element">
            <div className="spacer" id="pagination-component">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="7" className="text-center">
                            <h1 className="title font-bold">Easier than you think!</h1>
                            <h6 className="subtitle">Do you really think that learning English is boring? I can disprove this myth, because I know how to make classes interesting and varied!</h6>
                        </Col>
                    </Row>
                </Container>
            </div>
            <Container>
                <Row className="mb-5 justify-content-start">
                    <Col lg="3">
                        <img src={img1} alt="img" className="img-responsive img-rounded" width="150" />
                    </Col>
                    <Col lg="5">
                        <h2>Comfortable learning environment</h2>
                        <p className="m-t-15">This online course makes it easy to choose not only a convenient time and place for learning, but also your own pace.</p>
                    </Col>
                </Row>

                <Row className="mb-5 justify-content-end">
                    <Col lg="5">
                        <h2>We speak and work strictly in terms of CEFR</h2>
                        <p className="m-t-15">The content of the 15 course modules was developed in accordance with the Common European Framework of Reference for Languages. This will give you a clear idea of what your language should be like at the end of each level.</p>
                    </Col>
                    <Col lg="3">
                        <img src={img2} alt="img" className="img-responsive img-rounded" width="150" />
                    </Col>
                </Row>

                <Row className="justify-content-start">
                    <Col lg="3">
                        <img src={img3} alt="img" className="img-responsive img-rounded" width="200" />
                    </Col>
                    <Col lg="5">
                        <h2>Valuable Feedback</h2>
                        <p className="m-t-15">Upon completion of the modules, you will receive detailed feedback from the instructor. The instructor will help you understand incomprehensible issues, give you the missing knowledge and offer specific steps to achieve the result.
                        <br/><br/>You can easily contact the course instructor via chat, mail or audio-video call.</p>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default Peculiarities;
