import React from 'react';
import { HashLink as Link } from 'react-router-hash-link';
import './banner.css';

const HeaderBanner = () => {
    return (
        <div className="static-slider-head">
            <div class="header">
                <div class="inner-header">
                    <h2 className="title">English with<br />pleasuare</h2>
                    <h4 className="subtitle font-light">English language course <br />for specialists in the field of logistics</h4>
                    <Link to="/#consultation" className="btn btn-md m-t-30 btn-info-gradiant font-14">Start</Link>
                </div>

                <div>
                    <svg class="waves"
                        viewBox="0 24 150 28" preserveAspectRatio="none" shape-rendering="auto">
                        <defs>
                            <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z" />
                        </defs>
                        <g class="parallax">
                            <use xlinkHref="#gentle-wave" x="48" y="0" fill="rgba(255,255,255,0.7" />
                            <use xlinkHref="#gentle-wave" x="48" y="3" fill="rgba(255,255,255,0.5)" />
                            <use xlinkHref="#gentle-wave" x="48" y="5" fill="rgba(255,255,255,0.3)" />
                            <use xlinkHref="#gentle-wave" x="48" y="7" fill="#fff" />
                        </g>
                    </svg>
                </div>
            </div>
        </div>
    );
}

export default HeaderBanner;
