import React from 'react';
import { Container, Row, Col, Card } from 'reactstrap';

import img1 from '../../../assets/images/modules/module-image1.jpg';
import img2 from '../../../assets/images/modules/module-image2.jpg';
import img3 from '../../../assets/images/modules/module-image3.jpg';

const moduleLintText = "Start";

const Modules = () => {
    return (
        <div>
            <div className="spacer bg-light">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="7" className="text-center">
                            <h1 className="title font-bold">Modules</h1>
                            <h6 className="subtitle">Consistent completion of the course modules will allow you to achieve high results in learning English</h6>
                        </Col>
                    </Row>
                </Container>
            </div>

            <div className="blog-home2 spacer">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="8" className="text-center">
                            <h2 className="title">Course Modules</h2>
                            <h6 className="subtitle">Start studying the modules by clicking the Start button</h6>
                        </Col>
                    </Row>
                    <Row className="m-t-40 justify-content-center">
                        <Col lg="4" md="6">
                            <Card>
                                <a href="https://ru.padlet.com/xentosplus1/Bookmarks"><img className="card-img-top" src={img1} alt="wrappixel kit" /></a>
                                <h5 className="font-medium m-t-30"><a href="https://ru.padlet.com/xentosplus1/Bookmarks" className="link">Writing a business email</a></h5>
                                <p className="m-t-20">Completing the course, students will be able to know organizational patterns of writing business emails and cohesive devices for effective business communication.</p>
                                <a href="https://ru.padlet.com/xentosplus1/Bookmarks" className="linking text-themecolor m-t-10">{moduleLintText} <i className="ti-arrow-right"></i></a>
                            </Card>
                        </Col>
                        <Col lg="4" md="6">
                            <Card>
                                <div className="disabled">
                                    <a href="/#"><img className="card-img-top" src={img2} alt="wrappixel kit" /></a>
                                    <h5 className="font-medium m-t-30"><a href="/#" className="link">Telephone conversations</a></h5>
                                    <p className="m-t-20">Completing the course, students will be able to use specific terminology and grammar to competently and polite conduct telephone conversations with potential foreign suppliers and business partners.</p>
                                    <a href="/#" className="linking text-themecolor m-t-10">{moduleLintText} <i className="ti-arrow-right"></i></a>
                                </div>
                                <div className="date-pos bg-info-gradiant">Closed</div>
                            </Card>
                        </Col>
                        <Col lg="4" md="6">
                            <Card>
                                <div className="disabled">
                                    <a href="/#"><img className="card-img-top" src={img3} alt="wrappixel kit" /></a>
                                    <h5 className="font-medium m-t-30"><a href="/#" className="link">Transportation of goods</a></h5>
                                    <p className="m-t-20">Completing the course, students will be able to carry out transportation, delivery of various types of cargo, follow the processes of loading / unloading goods in a foreign company, using professional vocabulary and specific grammar.</p>
                                    <a href="/#" className="linking text-themecolor m-t-10">{moduleLintText} <i className="ti-arrow-right"></i></a>
                                </div>
                                <div className="date-pos bg-info-gradiant">Closed</div>
                            </Card>
                        </Col>
                    </Row>
                </Container>
            </div>
        </div>
    );
}

export default Modules;
