import React from 'react';
import { Container, Row, Col } from 'reactstrap';

import img1 from '../../../assets/images/ui/about-me.jpg';

// Особенности
const AboutMe = () => {
    return (
        <div id="element">
            <div className="spacer" id="pagination-component">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="7" className="text-center">
                            <h1 className="title font-bold" id="about-me">About your course instructor</h1>
                            <h6 className="subtitle">Let's get acquainted! My name is Ksenia Zolotukhina and I am your course instructor.</h6>
                        </Col>
                    </Row>
                </Container>
            </div>
            <Container>
                <Row className="mb-5 justify-content-start">
                    <Col lg="3" className="mr-3">
                        <img src={img1} alt="img" className="img-responsive img-rounded" width="150" />
                    </Col>
                    <Col lg="6">
                        <h2>I teach not only the language, but also the love for it!</h2>
                        <p className="m-t-15">I will reveal the potential and opportunities of each student.</p>
                        <h6>Educational goals and student achievements:</h6>
                        <ul>
                            <li>Improvement of knowledge and formation of necessary skills.</li>
                            <li>Improving knowledge of the general school curriculum.</li>
                            <li>Preparation for international exams (IELTS).</li>
                            <li>Preparation of English for IT specialists.</li>
                            <li>Preparation of English for Logistics.</li>
                            <li>I help to overcome the language barrier.</li>
                        </ul>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default AboutMe;
