import React from 'react';
import { Container, Row, Col, Form, FormGroup, Label, Input, Button } from 'reactstrap';

const ConsultationForm = () => {
    return (
        <div>
            <div className="spacer" id="forms-component">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="7" className="text-center">
                            <h1 className="title font-bold" id="consultation">Consultation</h1>
                            <h6 className="subtitle">I will help determine the level of your English proficiency and create the program according to your needs, interests and preferences.</h6>
                        </Col>
                    </Row>
                </Container>
            </div>
            <Container className="consultation-form">
                <Row className="justify-content-center mb-5">
                    <Col md="8">
                        <Form className="row justify-content-center">
                            <FormGroup className="col-8">
                                <Label htmlFor="name">Name</Label>
                                <Input type="text" className="form-control" id="name" placeholder="Enter Username" />
                            </FormGroup>
                            <FormGroup className="col-8">
                                <Label htmlFor="email">Email Address</Label>
                                <Input type="email" className="form-control" id="email" placeholder="Enter email" />
                            </FormGroup>
                            <FormGroup className="col-8">
                                <Label htmlFor="email">Phone</Label>
                                <Input type="email" className="form-control" id="phone" placeholder="Enter phone" />
                            </FormGroup>
                            <Col md="8">
                                <Button type="submit" className="btn btn-info waves-effect waves-light m-t-10">Sign up</Button>
                            </Col>
                        </Form>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default ConsultationForm;