import React from 'react';
import { Container, Row, Col } from 'reactstrap';


import img1 from '../../../assets/images/ui/goal1.jpg';
import img2 from '../../../assets/images/ui/goal2.jpg';
import img3 from '../../../assets/images/ui/goal3.jpg';

const Goals = () => {
    return (
        <div id="element">
            <div className="spacer" id="reactstrap-components">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="7" className="text-center">
                            <h1 className="title font-bold" id="goals">Goals</h1>
                            <h6 className="subtitle">Here you can check Demos we created based on WrapKit. Its quite easy to Create your own dream website &amp; dashboard in No-time.</h6>
                        </Col>
                    </Row>
                </Container>
            </div>
            <Container>
                <Row>
                    <Col lg="4" className="text-center m-b-30 grow">
                        <h4 className="card-title">Writing business emails</h4>
                        <h6 className="card-subtitle">Use specific terminology while writing business emails for effective business communication</h6>
                        <img src={img1} alt="img" className="img-circle" width="290" />
                        <p className="m-t-15 m-b-0"></p>
                    </Col>
                    <Col lg="4" className="text-center m-b-30 grow">
                        <h4 className="card-title">Oral communication</h4>
                        <h6 className="card-subtitle">Participation in professional discourse, coherent presentation of personal standpoint in speech</h6>
                        <img src={img2} alt="img" className="img-circle" width="290" />
                        <p className="m-t-15 m-b-0"></p>
                    </Col>
                    <Col lg="4" className="text-center m-b-30 grow">
                        <h4 className="card-title">Professional reading skills</h4>
                        <h6 className="card-subtitle">Read profession documentation in English, understand its content and express opinion</h6>
                        <img src={img3} alt="img" className="img-circle" width="290" />
                        <p className="m-t-15 m-b-0"></p>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default Goals;
